import { mapState, mapMutations } from 'vuex'
export default {
	data() {
		return {
			stateTab: false, //是否重新请求数据
			// loginState: null,
			showToLogin: false,
			showLoginOut: false,
			menuList: [{id: 1 , title:'我的活动',path: '/pages/userCenter/myActivities/myActivities'},
								{id: 2, title:'俱乐部',path: '/pages/userCenter/clubList/clubList'},
								{id: 3, title:'管理中心',path: '/pages/userCenter/managementCenter/managementCenter'},
								{id: 4, title:'新俱乐部入驻',path: '/pages/userCenter/clubEntry/clubEntry'},
								{id: 5,title:'常见问题',path: '/pages/userCenter/commonProblem/commonProblem'},],
			userData: {}
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),  //对全局变量hasLogin进行监控
	},
	created() {
		console.log('222')
		console.log(uni.getStorageSync('isLogin'))
		if(uni.getStorageSync('userInfo').userId > 0) {
			this.stateTab = false
		}else{
			this.stateTab = true
			this.showToLogin = true
			// console.log('调接口')
			// this.$http.get({
			// 	url: '/v1/rest/personalCenter/personalCenter',
			// 	data:{
			// 		userId: uni.getStorageSync('userInfo').userId,
			// 	}
			// }).then(resp => {
			// 	console.log(resp)
			// 	if(resp.status == 200) {
			// 		this.userData = resp.data
			// 		uni.setStorageSync('clubId', resp.data.clubId)
			// 	}
			// })
		}
	},
	onLoad() {
		// console.log('222')
	},
	onShow() {
		console.log('333')
		// const loginState = uni.getStorageSync('isLogin')
		// // console.log(loginState)
		// if(loginState) {
		// 	this.stateTab = false
		// }else{
		// 	this.stateTab = true
		// }
		if(!this.stateTab) {
			this.$http.get({
				url: '/v1/rest/personalCenter/personalCenter',
				data:{
					userId: uni.getStorageSync('userInfo').userId,
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.userData = resp.data
					uni.setStorageSync('clubId', resp.data.clubId)
				}
			})
		}
	},
	
	methods: {
		...mapMutations(['logout']),  //对全局方法login进行监控
		// 我的钱包
		toMyWallet(value) {
			uni.navigateTo({
			  url: '/pages/userCenter/myWallet/myWallet?totalMoney=' + value
			});
		},
		toLogin() {
			uni.navigateTo({
			  url: '/pages/login/login'
			});
		},
		// 退出登录
		doLoginout() {
			this.showLoginOut = true
		},
		handleConfirm() {
			console.log('确定退出')
			this.logout()
			uni.removeStorage({  //根据key值移除缓存数据
				key: 'isLogin'
			})
			this.userData = {}
			this.showToLogin = true
		},
		handleCancel() {
			this.showLoginOut = false
		}
	}
}
